html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: var(--color-black);
}

img {
  max-width: 100%;
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  font-size: initial;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.section-title {
  font-family: "Readex Pro", sans-serif;
  font-size: 42px;
  line-height: 52px;
  text-align: center;
  color: var(--color-black);

  @include small-tablet {
    font-size: 25px;
    line-height: 35px;
  }
}

.section-subtitle {
  font-family: "Readex Pro", sans-serif;
  font-size: 13px;
  line-height: 17px;
  text-align: center;
  color: var(--color-black);

  @include small-tablet {
    font-size: 12px;
    line-height: 16px;
  }
}

.btn {
  font-size: 18px;
  line-height: 22px;
  text-transform: capitalize;
  color: var(--color-black);
  border: 1px solid var(--color-black);
  background-color: transparent;
  border-radius: 200px;
  padding: 14px 53px;
  &--big {
    padding: 14px 63px;
    @include small-tablet {
      padding: 12px 50px;
    }
    @include mobile {
      padding: 8px 50px;
    }
  }
  @include small-tablet {
    font-size: 16px;
    line-height: 20px;
    padding: 12px 40px;
  }
  @include mobile {
    padding: 8px 40px;
  }
}

.burger {
  @include burger;

  @include small-tablet {
    display: flex;
    z-index: 100;
  }
}

.logo {
  &__img {
    object-fit: initial;
  }

  @include mobile {
    width: 120px;
  }
}
